.rate-book {

}

.rate-book label {
	font-size: 1rem;
	color: $white;
	background: $black;
	padding: 0.33em 1em;
	margin: 1em 0 1em calc(-16.66667% - 16.66667px + 25px);
	box-shadow: 2px 2px 10px rgba(0,0,0,0.3);

	@media screen and (min-width: 780px) {
		margin-left: -30px;
	}
}

.rate-book form {
	margin: 1em 0;
}

.rate-book select {
	padding: 0.33em 1em;
	margin: 1em 0 0;
	border: 2px solid #f36c9e;
	background: none;
	font-family: Montserrat, sans-serif;
	color: #f36c9e;
	font-weight: 700;

}

.rate-book option {
	background: #f36c9e;
}

.rate-book .button,
.rate-book select {
	display: inline-block;
	width: 50%;

	@media screen and (min-width: 780px) {
		width: 23%;
	}
}

.rate-book .button {
	padding: 0.55em 1em;
}

.rate-book p {
	font-size: 1rem;
	color: $white;
	text-shadow: 0.5px 0.5px 0px rgba(0,0,0,0.3);
}